import React, { Component } from "react";
import { reduxForm } from "redux-form";
import { connect } from "react-redux";
import paginationFactory from "react-bootstrap-table2-paginator";
import { berhasil, gagal } from "../Alert";
import HeaderCard from "../HeaderCard";
import { AxiosMasterPost } from "../Axios";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import { getSelesaiReparasi } from "../actions/selesaiReparasi";

const maptoState = (state) => {
  return {
    ListSelesaiReparasi: state.provinsi.ListSelesaiReparasi,
  };
};
class selesaiReparasi extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },

        {
          dataField: "nama_toko",
          text: "Nama Toko",
        },
        {
          dataField: "nama_sales",
          text: "Nama Sales",
        },
        {
          dataField: "level_customer",
          text: "Level Customer",
        },

        {
          dataField: "total_bruto",
          text: "Total Bruto",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_ongkos_reparasi",
          text: "Total Ongkos Reparasi",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "aksi",
          text: "Aksi",
          formatter: (col, row, index) => {
            return (
              <div className="row justify-content-center">
                <button
                  className="btn btn-success"
                  onClick={() => this.sendConfirmation(row.no_trx)}
                >
                  <i class="fa fa-check" aria-hidden="true"></i> Selesai
                  Reparasi
                </button>
              </div>
            );
          },
        },
      ],
      data: [
        {
          tanggal: "23/11/2020",
        },
      ],
      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
      listSales: [],
      listCustomer: [],
    };
  }
  sendConfirmation(data) {
    AxiosMasterPost("reparasi/post/selesai/" + data)
      .then(() => berhasil("Berhasil Selesai Reparasi"))
      .then(() => this.props.dispatch(getSelesaiReparasi()))
      .catch((err) =>
        gagal(
          `Gagal Selesai Reparasi, Check Koneksi Anda, Detial : ${err.response.data}`
        )
      );
  }

  componentDidMount() {
    this.props.dispatch(getSelesaiReparasi());
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Selesai Reparasi" />
          <div className="card-body">
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-12">
                  <div className="col-lg-12 mt-3">
                    <GlobalTabel
                      keyField="no_bon"
                      data={this.props.ListSelesaiReparasi}
                      columns={this.state.columns}
                      search
                      striped
                      hover
                      condensed
                      bordered={true}
                      headerClasses="header-table"
                      pagination={paginationFactory()}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

selesaiReparasi = reduxForm({
  form: "selesaiReparasi",
  enableReinitialize: true,
})(selesaiReparasi);
export default connect(maptoState, null)(selesaiReparasi);
